<template>
  <div>
    <v-card class="mx-auto overflow-hidden" height="100%">
      <table>
        <b-button v-b-modal.modal-prevent-closing>Thm bnh mi</b-button>
        <tr>
          <th>Tn bnh</th>
          <th>nh</th>
          <th>Gi tin</th>
          <th>Loi Bnh</th>
          <th>M t</th>
          <th>Hot ng</th>
        </tr>
        <tr v-for="item in products" :key="item._id">
          <td>{{ item.cake_name }}</td>
          <td><img :src="item.cake_image" alt="" style="height: 100px" /></td>
          <td>{{ item.price }}</td>
          <td>{{ item.cake_type }}</td>
          <td>{{ item.description }}</td>
          <td>
            <div style="display: flex">
              <button
                @click.prevent="deleteProducts(item._id)"
                class="btn-delete"
              >
                Xa
              </button>
              <button
                v-b-modal.modal-prevent
                @click.prevent="editproduct(item._id)"
                class="btn-edit"
              >
                Sa
              </button>
            </div>
          </td>
        </tr>
      </table>
    </v-card>
    <div>
      <b-modal
        id="modal-prevent-closing"
        ref="modal"
        title="Nhp thng tin bnh"
        @show="resetModal"
        @hidden="resetModal"
        @ok="handleOk"
      >
        <form ref="form" @submit.stop.prevent="addProduct">
          <b-form-group
            label="Tn bnh"
            label-for="name-input"
            invalid-feedback="Name is required"
            :state="nameState"
          >
            <b-form-input
              id="name-input"
              v-model="product.cake_name"
              :state="nameState"
              required
            ></b-form-input>
          </b-form-group>
          <b-form-group
            label="nh"
            label-for="image-input"
            invalid-feedback="Name is required"
            :state="nameState"
          >
            <b-form-input
              id="image-input"
              v-model="product.cake_image"
              :state="nameState"
              required
            ></b-form-input>
          </b-form-group>
          <b-form-group
            label="Gi tin"
            label-for="price-input"
            invalid-feedback="Name is required"
            :state="nameState"
          >
            <b-form-input
              id="price-input"
              v-model="product.price"
              :state="nameState"
              required
            ></b-form-input>
          </b-form-group>
          <b-form-group
            label="Loi bnh"
            label-for="type-input"
            invalid-feedback="Name is required"
            :state="nameState"
          >
            <b-form-input
              id="type-input"
              v-model="product.cake_type"
              :state="nameState"
              required
            ></b-form-input>
          </b-form-group>
          <b-form-group
            label="M t"
            label-for="description-input"
            invalid-feedback="Name is required"
            :state="nameState"
          >
            <b-form-input
              id="description-input"
              v-model="product.description"
              :state="nameState"
              required
            ></b-form-input>
          </b-form-group>
        </form>
      </b-modal>
      <!-- update -->
      <b-modal
        id="modal-prevent"
        ref="modal"
        title="Sa thng tin bnh"
        @show="resetModal"
        @hidden="resetModal"
        @ok="handleOk2"
      >
        <form ref="form" @submit.stop.prevent="updateProduct">
          <b-form-group
            label="Tn bnh"
            label-for="name-input"
            invalid-feedback="Name is required"
            :state="nameState"
          >
            <b-form-input
              id="name-input"
              v-model="oldProduct.cake_name"
              :state="nameState"
              required
            ></b-form-input>
          </b-form-group>
          <b-form-group
            label="nh"
            label-for="image-input"
            invalid-feedback="Name is required"
            :state="nameState"
          >
            <b-form-input
              id="image-input"
              v-model="oldProduct.cake_image"
              :state="nameState"
              required
            ></b-form-input>
          </b-form-group>
          <b-form-group
            label="Gi tin"
            label-for="price-input"
            invalid-feedback="Name is required"
            :state="nameState"
          >
            <b-form-input
              id="price-input"
              v-model="oldProduct.price"
              :state="nameState"
              required
            ></b-form-input>
          </b-form-group>
          <b-form-group
            label="Loi bnh"
            label-for="type-input"
            invalid-feedback="Name is required"
            :state="nameState"
          >
            <b-form-input
              id="type-input"
              v-model="oldProduct.cake_type"
              :state="nameState"
              required
            ></b-form-input>
          </b-form-group>
          <b-form-group
            label="M t"
            label-for="description-input"
            invalid-feedback="Name is required"
            :state="nameState"
          >
            <b-form-input
              id="description-input"
              v-model="oldProduct.description"
              :state="nameState"
              required
            ></b-form-input>
          </b-form-group>
        </form>
      </b-modal>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "AdminProduct",
  data() {
    return {
      products: [],
      product: {
        cake_name: "",
        cake_image: "",
        price: "",
        cake_type: "",
        description: "",
      },
      drawer: false,
      group: null,
      oldProduct: {},
      nameState: null,
    };
  },
  async created() {
    const res = await axios.get("cake");
    this.products = res.data;
    console.log(this.products);
  },
  methods: {
    async deleteProducts(_id) {
      axios.delete(`cake/${_id}`).then(() => {
        for (let i = 0; i < this.products.length; i++) {
          if (this.products[i]._id == _id) {
            this.products.splice(i, 1);
            break;
          }
        }
      });
      alert("Xa thnh cng");
    },
    async addProduct() {
      axios.post("cake/create", this.product).then(() => {
        this.products.push(this.product);
        console.log(this.product);
        this.$nextTick(() => {
          this.$bvModal.hide("modal-prevent-closing");
        });
      });
      window.location.reload();
      alert("Thm thnh cng");
    },
    async editproduct(_id) {
      const res = await axios.get(`cake/${_id}`);
      this.oldProduct = res.data[0];
      console.log(this.oldProduct);
    },
    async updateProduct() {
      axios.put(`cake/${this.oldProduct._id}`, this.oldProduct).then(() => {
        for (let i = 0; i < this.products.length; i++) {
          if (this.products[i] == this.oldProduct._id) {
            this.products.splice(i, 1, this.oldProduct);
            console.log(i);
            break;
          }
        }
        alert("Cp nht thnh cng");
      });
      this.$nextTick(() => {
        this.$bvModal.hide("modal-prevent");
      });
      window.location.reload();
    },
    checkFormValidity() {
      const valid = this.$refs.form.checkValidity();
      this.nameState = valid;
      return valid;
    },
    handleOk(bvModalEvt) {
      // Prevent modal from closing
      bvModalEvt.preventDefault();
      // Trigger submit handler
      this.addProduct();
    },
    handleOk2(bvModalEvt) {
      // Prevent modal from closing
      bvModalEvt.preventDefault();
      // Trigger submit handler
      this.updateProduct();
    },
    resetModal() {
      this.nameState = null;
    },
  },
};
</script>
<style scoped>
table {
  font-family: arial, sans-serif;
  border-collapse: collapse;
  width: 80%;
  margin: 50px auto;
  font-size: 14px;
}

td,
th {
  border: 1px solid #dddddd;
  text-align: center;
  padding: 8px;
}

tr:nth-child(even) {
  background-color: #dddddd;
}
.btn-delete {
  padding: 10px;
  border-radius: 5px;
  color: #dddddd;
  background-color: orangered;
  margin-right: 10px;
}
.btn-edit {
  background-color: blue;
  padding: 10px;
  border-radius: 5px;
  color: #dddddd;
}
.v-item-group {
  margin-top: 50%;
}
</style>

